.row {
  padding-top: 20px;
  margin-left: 20px;
  color: #fff;
}

.posters {
  display: flex;
  padding: 20px;
  overflow-x: scroll;
  overflow-y: hidden;
}
.poster:hover {
  transform: scale(1.07);
}

.posters::-webkit-scrollbar {
  display: none;
}

.poster {
  max-height: 250px;
  margin-right: 10px;
}

.small_poster {
  max-height: 150px;
  margin-right: 10px;
}
.small_poster:hover {
  transform: scale(1.07);
}
