.body {
  max-width: 100%;
}

.navbar {
  position: fixed;
  color: #e6e6e6;
  top: 0;
  width: 100%;
  height: 30px;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  background-color: #111;
}

.logo {
  position: fixed;
  left: 20px;
  width: 80px;
}

.avatar {
  position: fixed;
  right: 20px;
  width: 30px;
}

.navList {
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  width: 700px;
  font-size: 14px;
  margin-left: 100px;
  font-weight: 500;
}

.navList p:hover,
.avatar:hover,
.logo:hover {
  cursor: pointer;
  color: rgb(134, 134, 134);
}
